
.profile-box {

  &__img-box {
    width: 30px;
    height: 30px;
    margin: 0 auto;
    border-radius: 50%;
    overflow: hidden;
  }

  &__img {
    width: 100%;
  }

  &__welcome-txt {
    margin-top: 4px;
    font-weight: 700;
    font-size: 14px;
    text-align: center;
    line-height: 1.43;
  }
}

.pg {

  &__list {
    display: flex;
    justify-content: center;
    gap: 0 4px;
  }

  &__item {
    position: relative;
    z-index: 1;
  }

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: roboto;
    background: #fff;
  }

  &__num {
    width: 30px;
    height: 30px;
    font-weight: 700;
    font-size: 14px;
    color: #999;
    border: 1px solid #999;
    border-radius: 4px;
    overflow: hidden;

    &.active {
      color: #ec6602;
      border-color: #ec6602;
      background: #fdddcb;
    }
  }

  &__arrow {
    width: 30px;
  }
}

// 컨테이너 상단
.ch-h {
  display: flex;
  justify-content: space-between;

  &__target {
    position: relative;
    padding-right: 20px;
    font-size: 14px;
    line-height: 20px;

    &.on {
      font-weight: 700;
    }

    &:after {
      content: '';
      position: absolute;
      top: 4px;
      right: 4px;
      width: 7px;
      height: 13px;
      background: url($imgUrl + 'admin/ch-h-arrow.png') 0/100%;
    }

    &:last-child {

      &:after {
        display: none;
      }
    }
  }
}

// 게시판 검색
.sch-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 8px;
  border-radius: 5px;
  border: solid 1px #dedede;
  background-color: #f6f6f6;

  &__sub {
    display: flex;
    align-items: center;
  }

  &__btn-sch {
    width: 100px;
  }
}

.chip {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: 72px;
  height: 32px;
  border: 2px solid #000;
  border-radius: 16px;

  &--active {
    color: #ec6602;
    font-weight: 500;
    font-size: 12px;
    background: #fdddcb;
    border-color: #ec6602;
  }

  &--disabled {
    color: #999;
    font-weight: 500;
    font-size: 12px;
    background: #e6e6e6;
    border-color: #999;
  }

  &--make-qr {
    color: #009999;
    font-weight: 500;
    font-size: 12px;
    background: #def9f5;
    border-color: #009999;
  }
}

.lpop {
  display: none;
  cursor: move;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 830px;
  z-index: 50;
  transform: translate(-50%, -50%);
  background: #fff;
  border-top: 4px solid #3dc3b0;
  box-shadow: 0 7px 17px 0 rgba(0, 0, 0, 0.16);

  &.open {
    display: block;
  }

  &__container {
    position: relative;
  }

  &__head {
    display: flex;
    align-items: center;
    height: 46px;
    padding-left: 20px;
    background: #DEF9F5;
  }

  &__tit {
    color: #009999;
    font-weight: 500;
    font-size: 18px;
  }

  &__content {
    padding: 40px 60px;
  }

  &__cont-tit {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 22px;

    &.lg {
      font-size: 30px;
    }
  }

  &__user-tbl {
    width: 100%;
    border: 1px solid #dedede;

    .col01 {
      width: 150px;
    }

    .col02 {
      width: auto;
    }

    tbody {

      tr {
        border-bottom: 1px solid #dedede !important;
      }

      th {
        font-weight: 700;
        font-size: 14px;
        background: #ededed;

        .label {

          &.required:after {
            content: '*';
            color: #ec6602;
            font-size: 1em;
          }
        }
      }

      td {
        height: 50px;
        padding: 5px !important;
      }
    }
  }

  .btn-close {
    position: absolute;
    top: 9px;
    right: 15px;
    width: 24px;
  }

  &__txt {
    font-size: 22px;
    line-height: 28px;
  }

  &.remake-qr {

    .btn-wrap {
      margin-top: 130px;
    }
  }
}

.act-tg {
  display: flex;
  align-items: center;

  &__switch {
    position: relative;
    display: inline-block;
    width: 64px;
    height: 32px;
  }
  
  &__input {
    display:none;
  }

  &__slider {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5px;
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;

    i {
      font-style: normal;
      color: #fff;
      opacity: 0.4;
      font-size: 14px;
      font-weight: 500;
      user-select: none;
    }
  }
  
  &__slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    right: 4px;
    bottom: 3px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  &__input:checked + &__slider {
    background-color: #ec6602;
  }
  
  &__input:focus + &__slider {
    box-shadow: 0 0 1px #ec6602;
  }
  
  &__input:checked + &__slider:before {
    transform: translateX(-30px);
  }
  
  /* Rounded sliders */
  &__slider.round {
    border-radius: 34px;
  }
  
  &__slider.round:before {
    border-radius: 50%;
  }

  &__txt {
    margin-left: 12px;
  }
}

.alert {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 100;
  min-width: 558px;
  padding: 16px 23px;
  transform: translate(-50%, -50%);
  border: 1px solid #e6e6e6;
  background: #fff;
  box-shadow: 0 13px 17px 0 rgba(0, 0, 0, 0.16);

  &.open {
    display: block;
  }

  &__tit {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 18px;
  }

  &__txt {
    font-size: 16px;
    line-height: 24px;
  }

  .btn-wrap {
    justify-content: flex-end;
  }
}

.loading {
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 90;
  background-color: rgba(255, 255, 255, 0.8);

  &.on {
    display: flex;
  }

  .loader {
    width: 50px;
    padding: 8px;
    aspect-ratio: 1;
    border-radius: 50%;
    background: #25b09b;
    --_m: 
      conic-gradient(#0000 10%,#000),
      linear-gradient(#000 0 0) content-box;
    -webkit-mask: var(--_m);
            mask: var(--_m);
    -webkit-mask-composite: source-out;
            mask-composite: subtract;
    animation: l3 1s infinite linear;
  }
  @keyframes l3 {to{transform: rotate(1turn)}}
}

.tbl-row-cnt {
  margin-bottom: 10px;

  .trc-txt {
    color: #009999;
    font-weight: 500;
    font-size: 22px;
  }
}