
.radio-group {
  display: flex;
}

.radio {
  display: flex;
  align-items: center;
  margin-left: 20px;

  &:first-child {
    margin-left: 0;
  }
  
  &__input {
    appearance: none;
    width: 20px;
    height: 20px;
    margin-right: 5px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url($imgUrl + 'admin/radio-default.png');

    &:checked {
      background-image: url($imgUrl + 'admin/radio-checked.png');
    }
  }

  &__label {
    font-weight: 500;
    font-size: 16px;
    white-space: nowrap;
  }
}

.input-txt {
  height: 44px;
  padding: 0 15px;
  font-size: 16px;
  border-radius: 5px;
  border: solid 1px #dedede;
  background-color: #fff;

  &[readonly] {
    color: #999;
    background: #f6f6f6;
    border: 1px solid #dedede;
  }
}

.select {
  display: flex;
  align-items: center;
  height: 40px;

  &__label {
    margin-right: 10px;
    font-weight: 500;
    font-size: 16px;
    white-space: nowrap;
  }

  &__select {
    appearance: none;
    height: 44px;
    padding: 0 20px;
    background-repeat: no-repeat;
    background-size: 30px;
    background-position: right 5px top 50%;
    background-image: url($imgUrl + 'admin/select-bg.png');
    border: solid 1px #dedede;
    background-color: #fff;
    border-radius: 5px;
  }
}

.sch-box {

  .select__select {
    width: 123px;
  }

  .input-txt {
    width: 400px;
  }
}

.custom-cbox {
  position: relative;
  z-index: 1;
  appearance: none;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0 0;
  background-image: url($imgUrl + 'admin/custom-cbox.png');
  background-color: #fff;

  &[disabled] {
    background-color: #f6f6f6;
    background-image: url($imgUrl + 'admin/custom-cbox-disabled.png');

    &:checked {
      background-image: url($imgUrl + 'admin/custom-cbox-disabled.png');
    }
  }

  &:checked {
    background-image: url($imgUrl + 'admin/custom-cbox-checked.png');
  }
}

.datepicker {
  display: flex;
  align-items: center;
  position: relative;

  .input-txt {
    width: 140px;
    background: #fff url($imgUrl + 'admin/ico-calendar.png') right 7px top 50%/30px no-repeat;
  }

  .datepicker__wave {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 18px;
  }
}

.input__label {
  white-space: nowrap;
}

@media screen and (max-width: 1500px) {
  
  .sch-box {

    .input-txt {
      width: 200px;
    }
  }

  .datepicker {
  
    .input-txt {
      width: 140px;
    }
  }  
}
