.page-title {
  font-weight: 700;
  font-size: 30px;
  line-height: 44px ;
}

.tbl-list {
  background: #fff;
  border-top: 1px solid #dedede;

  &.tbl-user-list {

    .tul-col01 {
      width: 60px;
    }

    .tul-col02 {
      width: 15%;
    }

    .tul-col03 {
      width: 15%;
    }

    .tul-col04 {
      width: 15%;
    }

    .tul-col05 {
      width: auto;
    }

    .tul-col06 {
      width: 15%;
    }

    .tul-col07 {
      width: 112px;
    }

    tbody tr:nth-child(even) {
      height: 0;
      border-bottom: 0;

      td {
        height: 0;
        padding: 0 5px;
      }
    }
  }

  &.hospitel-list {

    .hspt-l-col01 {
      width: 50px;
    }    

    .hspt-l-col02 {
      width: 60px;
    }    

    .hspt-l-col03 {
      width: auto;
    }    

    .hspt-l-col04 {
      width: auto;
    }    

    .hspt-l-col05 {
      width: 145px;
    } 
    
    .lpop-row {
      border-bottom: 0;
    }

    .lpop-row td {
      height: 0;
      padding: 0;
    }
  }

  &.holyday-list {

    .hol-l-col01 {
      width: 50px;
    }    

    .hol-l-col02 {
      width: 60px;
    }    

    .hol-l-col03 {
      width: auto;
    }    

    .hol-l-col04 {
      width: auto;
    }    

    .hol-l-col05 {
      width: 145px;
    } 
    
    .lpop-row {
      border-bottom: 0;
    }

    .lpop-row td {
      height: 0;
      padding: 0;
    }    

    .last-day {
      background: #eee;
      pointer-events: none;
    }
  }

  &.eq-list {

    .eq-col01 {
      width: 60px;
    }

    .update-txt {
      display: block;
      color: #999;
      font-size: 12px;
      line-height: 1.42;
      text-align: center;
      white-space: nowrap;

      &.on {
        color: #3dc3b0;
      }
    }
  }

  &.qr-issue-list {

    .qr-wrap {
      display: flex;
      align-items: center;
    }

    .qr-img {
      width: 70px;
      margin: 0 20px;
    }

    .wrong-code {
      width: 100%;
      height: 100%;
      color: #b21b5c;
      background: #fdddcb;
    }

    th {
      
      &:nth-child(4) {
        text-align: left;
        padding-left: 60px;
      }
    }

    td {
      height: 99px;

      &:nth-child(4) {
        text-align: left;
        padding-left: 40px;
      }
    }
  }

  &.qr-issue-history {

    .col01 {
      width: 80px;
    }

    .col07 {
      width: 160px;
    }

    .col08 {
      width: 160px;
    }

    .qr-img {
      width: 70px;
      margin: 0 auto;
      text-align: center;
    }

    td {
      height: 99px;
    }
  }

  &.qr-print {

    .col01 {
      width: 60px;
    }

    .col02 {
      width: 80px;
    }

    .col03 {
      width: 160px;
    }

    .col07 {
      width: 160px;
    }

    .col08 {
      width: 160px;
    }

    .qr-img {
      width: 70px;
      margin: 0 auto;
      text-align: center;
    }

    td {
      height: 99px;
    }
  }

  thead {

    tr {
      border-bottom: 2px solid #3dc3b0;
    }

    th {
      height: 50px;
      font-size: 14px;
      background-color: #f6f6f6;
    }
  }

  tbody {

    tr {
      border-bottom: 1px solid #dedede;
    }

    .pointer-cursor {

        &:hover {
          background: #def9f5;
        }
    }

    td {
      height: 62px;
      padding: 10px 5px;
      font-size: 14px;
      text-align: center;

      .email {
        white-space: nowrap;
      }
    }
  }

  .custom-cbox {
    position: relative;
    top: 3px;
    cursor: pointer;
  }  
}

.btn-wrap-after-tbl {
  display: flex;
  position: relative;
  gap: 0 5px;
  margin-top: 17px;

  .inner {
    display: flex;
    justify-content: flex-end;
    position: absolute;
    gap: 0 5px;
    width: 100%;
    bottom: -65px;
    right: 0;
  }
}

.reg-eq {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  margin: 124px -50px -40px;
  padding: 0 3vw;
  background: #fdddcb;

  &__col {
    display: flex;
    align-items: center;
  }

  &__label {
    margin-right: 15px;
    font-weight: 700;
    font-size: 18px;
  }

  &__input-box {
    display: flex;
    align-items: center;
    position: relative;
    max-width: 500px;
    min-width: 300px;
    width: 28vw;
    height: 44px;
    margin-right: 6px;
    padding: 0 8px;
    background: #fff;
    border-radius: 22px;
  }

  &__btn-file {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 78px;
    height: 30px;
    color: #fff;
    font-weight: 500;
    font-size: 14px;
    border-radius: 15px;
    background-color: #666;
  }

  &__input-file {
    display: none;
  }

  &__input-txt {
    width: calc(100% - 80px);
    height: 44px;
    margin-left: 10px;
    border: 0;
    border-radius: 22px;
    outline: none;
  }

  &__del-file {
    display: none;
    position: absolute;
    top: 10px;
    right: 14px;
    width: 24px;

    &.show {
      display: block;
    }
  }

  &__guide {
    margin-right: 12px;
    color: #666;
    font-size: 14px;
  }
}

.print-page {
  padding: 30px 40px;
  border-radius: 6px;
  border: solid 1px #dedede;
  background-color: #fff;

  .pp-head {
    display: flex;
    align-items: center;
  }

  .pp-tit {
    margin-right: 26px;
    font-weight: 700;
    font-size: 30px;
  }

  .pp-list {
    display: flex;
    flex-wrap: wrap;

    &__inner {
      width: 502px;
      height: 323px;
      margin-right: -1px;
      margin-bottom: -1px;
      padding: 20px 20px 20px 30px;
      border: solid 1px #e6e6e6;
      background-color: #fff;
    }

    &__logo {
      display: flex;
      justify-content: flex-end;
      width: 100%;

      img {
        width: 115px;
      }
    }

    &__qr-info {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__qr {
      width: 176px;

      img {
        width: 100%;
      }
    }
    
    &__info {
      max-width: 60%;
      width: calc(100% - 176px);
      text-align: right;
    }

    &__label {
      display: block;
      width: 100%;

      img {
        max-width: 189px;
        width: 100%;
      }
    }

    &__data {
      margin-top: 10px;
      font-family: 'malgun gothic';
      font-size: 16px;
      line-height: 21px;

      strong {
        width: 64px;
        margin-right: 5px;
      }
    }

    &__guide {
      margin-top: 27px;
      font-weight: 900;
      font-size: 12px;
      font-family: 'malgun gothic';
      line-height: 16px;
    }

    &__step {
      display: flex;
      margin-top: 5px;
    }

    &__step-head {
      width: 63px;
      font-weight: 900;
      font-size: 12px;
      font-family: 'malgun gothic';
      line-height: 16px;
    }

    &__step-desc {
      width: calc(100% - 67px);
      margin-left: 4px;
      font-size: 12px;
      font-family: 'malgun gothic';
      line-height: 16px;
    }
  }
}

@media screen and (max-width: 1600px) {
  .reg-eq {
    height: 80px;
    padding: 0 2vw;
  
    &__label {
      font-size: 16px;
    }
  
    &__btn-file {
      left: 8px;
      width: 65px;
      font-size: 13px;
    }

    &__btn-reg {
      font-size: 14px;
    }
  
    &__guide {
      font-size: 12px;
    }
  }
}

.connection-failed {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background: #ededed;

  .cf-box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 540px;
    height: 540px;
    border-radius: 40px;
    background: #fff;
  }

  .admin-logo {
    text-align: center;

    img {
      width: 164px;
    }
  }

  .cf-tit {
    margin-top: 30px;
    font-weight: 700;
    font-size: 39px;
    text-align: center;
  }

  .cf-txt {
    font-weight: 500;
    font-size: 24px;
    text-align: center;
  }

  .cf-txt2 {
    padding-top: 110px;
    color: #666;
    font-size: 14px;
    text-align: center;
    background: url($imgUrl + 'admin/login-failed.png') 50% 20px/60px no-repeat;
  }

  .admin-logo2 {
    margin-top: 20px;
    text-align: center;

    img {
      width: 114px;
    }
  }
}
