.btn-default {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  padding: 0 22px;
  border-radius: 20px;
  color: #fff;
  font-weight: 500;
  font-size: 18px;

  &.confirm {
    background: #3dc3b0;
  }

  &.confirm2 {
    background: #ec6602;
  }

  &.gray {
    background: #999;
  }

  &.delete {
    color: #666;
    background: #dedede;
  }

  &:hover {
    opacity: 0.8;
  }
}

.btn-empty {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 36px;
  padding: 8px 14px;
  border-radius: 6px;
  border: solid 1px #999;
  background-color: #fff;
  font-size: 14px;
}

.process-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  padding: 0 28px;
  border-radius: 28px;

  &.w-full {
    width: 100%;
  }

  &.confirm {
    background: #ec6602;

    .process-btn__txt {
      color: #fff;
    }
  }

  &.confirm-gray {
    background: #999;

    .process-btn__txt {
      color: #fff;
    }
  }

  &.confirm-green {
    background: #3dc3b0;

    .process-btn__txt {
      color: #fff;
    }
  }

  &.cancel {
    background: #dedede;
  }

  &[disabled] {
    background: #eee;

    .process-btn__txt {
      color: #666;
      font-size: 18px;
    }
  }

  &__txt {
    font-weight: 500;
    font-size: 18px;
  }
}

.btn-wrap {
  display: flex;
  justify-content: center;
  gap: 0 5px;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 44px;
  border-radius: 8px;

  &.w-f {
    width: 100%;
  }

  &:hover {
    opacity: 0.7;
  }

  &.confirm {
    color: #fff;
    font-size: 16px;
    background: #099;
  }

  &.remove {
    color: #fff;
    font-size: 16px;
    background: #999;
  }
}

.btn-open-pop {
  display: flex;
  align-items: center;
  height: 56px;
  padding: 0 40px 0 12px;
  text-align: left;
  border: 1px solid #eee;
  border-radius: 8px;
  background: url($imgUrl + 'btn-pop-open-arrow.png') calc(100% - 10px) 50%/24px no-repeat;

  &__txt {
    font-size: 14px;
  }
}