// 임시
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');

*,
*:before,
*:after {box-sizing: border-box;}

html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  font-family: 'Noto Sans kr', -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Apple SD Gothic Neo", Arial, sans-serif;
  color: #222;
  letter-spacing: -0.025em;
  &.isScrollLock,
  &.isLayerShow,
  &.isLoading {overflow: hidden;}
}
h1,h2,h3,h4,h5,h6,p,figure,ul,ol,dl,dd,fieldset,pre {margin: 0;}
ol, ul, td, fieldset {padding: 0;}
input, select, textarea, button, pre, a, code {
  font : {
    family: inherit;
    size: inherit;
    weight: inherit;
  }
  line-height: inherit;
  letter-spacing: inherit;
  color: inherit;
  text-decoration: none;
}
li {list-style: none;}
table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
  border: 0;
}
fieldset, img {border: 0;}
img {
  vertical-align: top;
  max-width: 100%;
}

label {cursor: pointer;}
label, td img {vertical-align: middle;}
h1,h2,h3,h4,h5,strong,b,th {font-weight: 700;}
legend, caption {display: none;}
address, em {font-style: normal;}
button {
  margin: 0;
  padding: 0;
  overflow: visible;
  cursor: pointer;
  background: none;
  border: 0;
  border-radius: 0;
}
pre {white-space: pre-line;}
