.wrap {
  position: relative;
  min-width: 1400px;
  height: 100%;
  overflow: hidden;
}

.wrap-flex {
  display: flex;
  height: 100%;
}

.header {
  width: 230px;
  height: 100%;
  background: #fff;

  .logo-wrap {
    display: flex;
    justify-content: center;
    padding: 30px 0;
  }

  .logo {
    width: 164px;
  }

  .header-container {
    height: calc(100% - 137px);
    overflow-y: auto;
  }

  .nav-container {
    height: calc(100% - 194px);
    overflow-y: auto;
    margin-top: 40px;
    padding-left: 10px;
    
    &::-webkit-scrollbar-track {
      border-radius: 10px;
    }

    &::-webkit-scrollbar
    {
      width: 0;
    }

    &::-webkit-scrollbar-thumb
    {
      border-radius: 10px;
      -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0);
    }
  }

  .header-foot {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 15px;

    .logout {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 80px;
      height: 40px;
      border: 1px solid #e6e6e6;
      border-radius: 20px;

      .txt {
        position: relative;
        padding-left: 22px;
        font-size: 10px;

        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 16px;
          height: 16px;
          background: url($imgUrl + 'admin/logout.png') 0/100% no-repeat;
        }
      }
    }

    .connecting-time {
      color: #999;
      font-size: 10px;
    }
  }
}
.gnb {

  &__category {
    margin-bottom: 10px;
    padding: 20px 25px;
    border-radius: 20px 0 0 20px;

    &.on {
      background: #f6f6f6;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &:nth-of-type(1) {

      .gnb__category-txt {
        background-image: url($imgUrl + 'admin/nav-title-ico-person.png');
      }
    }

    &:nth-of-type(2) {

      .gnb__category-txt {
        background-image: url($imgUrl + 'admin/nav-title-ico-data.png');
      }
    }

    &:nth-of-type(3) {

      .gnb__category-txt {
        background-image: url($imgUrl + 'admin/nav-title-ico-qr.png');
      }
    }
  }

  &__category-txt {
    padding-left: 23px;
    color: #009999;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    background-repeat: no-repeat;
    background-size: 18px;
    background-position: 0 1px;
  }

  &__sub {
    margin-top: 15px;
  }

  &__sub-item {
    margin-bottom: 5px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__sub-btn {
    display: flex;
    align-items: center;
    height: 30px;
    padding-left: 20px;
    color: #333;
    font-weight: 500;
    font-size: 14px;
    border-radius: 15px;

    &.on {
      color: #fff;
      background: #3dc3b0;
    }
  }
}

.container-wrap {
  width: calc(100% - 230px);
  height: 100%;
  overflow: hidden;
  padding-top: 30px;
}

.container {
  height: 100%;
  overflow-y: auto;
  padding: 40px 50px;
  background: #ededed;
  border-radius: 40px 0 0 0;
}
