// 반복문을 사용하여 마진을 1에서 100까지 늘리는 믹스인
@mixin generate-margins {
  @for $i from 1 through 100 {
    .mg-#{$i} {
      margin: #{$i}px;
    }
  }
}

// 마진을 방향별로 줄 때
@mixin generate-margins-direction {
  @for $i from 1 through 100 {
    .mt-#{$i} {
      margin-top: #{$i}px;
    }
    .mr-#{$i} {
      margin-right: #{$i}px;
    }
    .mb-#{$i} {
      margin-bottom: #{$i}px;
    }
    .ml-#{$i} {
      margin-left: #{$i}px;
    }
  }
}

@include generate-margins; // 반복문을 사용하여 마진을 1에서 100까지 늘리는 믹스인
@include generate-margins-direction; // 마진을 방향별로 줄 때

// 이미지 경로
$imgUrl: '/assets/img/';
