.d-flex {
  display: flex;

  &.gap0-10 {
    gap: 0 10px;
  }

  &.jc-sb {
    justify-content: space-between;
  }

  &.jc-fe {
    justify-content: flex-end;
  }
}

.check-wrap {

  .checkbox {

    & + .checkbox-next {
      margin-left: 5px;
    }
  }
  .checkbox-next {
    width: calc(100% - 21px);
    margin-left: 5px;
  }
}

.checkbox {
  width: 16px;  
  height: 16px;
  margin: 0;
}

.w-f {
  width: 100%;
}

.pointer-cursor {
  cursor: pointer;
}

.tal {
  text-align: left;
}

.tac   {
  text-align: center;
}